import md5 from "@/common/SDK/md5.min.js";
export default {
	data() {
		return {
			phoneNumber:"",
			name:'',
			code:'',
			passwd:"",
			getCodeText:'获取验证码',
			getCodeBtnColor:"#ffffff",
			getCodeisWaiting:false
		}
	},
	onLoad() {

	},
	methods: {
		Timer(){},
		getCode(){
			uni.hideKeyboard()
			if(this.getCodeisWaiting){
				return ;
			}
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 
			this.getCodeText = "发送中..."
			this.getCodeisWaiting = true;
			this.getCodeBtnColor = "rgba(255,255,255,0.5)"
			//示例用定时器模拟请求效果
			setTimeout(()=>{
				uni.showToast({title: '验证码已发送',icon:"none"});
				//示例默认1234，生产中请删除这一句。
				this.code=1234;
				this.setTimer();
			},1000)
		},
		setTimer(){
			let holdTime = 60;
			this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(()=>{
				if(holdTime<=0){
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ffffff";
					this.getCodeText = "获取验证码"
					clearInterval(this.Timer);
					return ;
				}
				this.getCodeText = "重新获取("+holdTime+")"
				holdTime--;
				
			},1000)
		},
		doReg(){
			var self=this;
			
			uni.hideKeyboard()
			//模板示例部分验证规则
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			} 
			//示例验证码，实际使用中应为请求服务器比对验证码是否正确。
			// if(this.code!=1234){ 
			// 	uni.showToast({title: '验证码不正确',icon:"none"});
			// 	return false; 
			// }
			uni.showLoading({
				title: '提交中...'
			})
			
			//注册
			uni.request({
				url:self.$url+'register/register',
				data:{name:self.name,username:self.phoneNumber,password:md5(self.passwd),code:self.code},
				method:'POST',
				success:function(res){
					if(res.data.code==1){
						uni.showToast({
							title:'注册成功'
						})
						setTimeout(function(){
							uni.navigateBack();
							},1000
						)
					}else{
						uni.showToast({
							title:res.data.msg,
						})
					}
				}
			})
			
			
		},
		toLogin(){ 
			uni.hideKeyboard()
			uni.redirectTo({url: 'login'});
			uni.navigateBack();
		}
	}
}
